import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable, firstValueFrom } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class CheckOutService {
  private url: string = `${environment.urlWeb}`;
  private _status: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _address: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _idtransaccion: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  /**
   * Constructor
   */
  constructor(private _httpClient: HttpClient) {}

  /**
   * Getter for user types
   */
  get status$(): Observable<any[]> {
    return this._status.asObservable();
  }

  /**
   * Getter for address
   */
  get address$(): Observable<any[]> {
    return this._address.asObservable();
  }

  /**
   * Getter for idTransaccion
   */
  get idTransaccion$(): Observable<any[]> {
    return this._idtransaccion.asObservable();
  }

  generateOrder(data: any): Observable<any> {
    return this._httpClient.post(`${this.url}pagos/crearIntento`, data);
  }

  sendPayment(token: string, id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/generarMetodo/${id}`, {
        token: token,
      })
    );
  }

  getOrder(id: any): Observable<any> {
    return this._httpClient.get(`${this.url}pagos/obtenerIntento/${id}`);
  }

  confirmPayment(id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/confirmar/${id}`, {})
    );
  }

  setStatus(status:any): any{
    this._status.next(status);
  }

  setAddress(address:any): any{
    this._address.next(address);
  }

  setIdTransaccion(id:any): any{
    this._idtransaccion.next(id);
  }

  setAddessPulsera(data:any, id:any){
    return this._httpClient.post(`${this.url}manillas/update/${id}`, data);
  }
}