import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from 'src/app/core/user/user.service';
import { PaymentContinueService } from './payment-continue.service';
import { Subject, takeUntil } from 'rxjs';
import Swal from 'sweetalert2';
import { ViewportScroller } from '@angular/common';
import { CheckOutService } from '../manillas/checkout-page/checkout-page.service';

declare global {
  interface Window {
    Stripe?: any;
  }
}

@Component({
  selector: 'app-payment-continue',
  templateUrl: './payment-continue.component.html',
  styleUrls: ['./payment-continue.component.scss']
})
export class PaymentContinueComponent implements OnInit {
  Toast:any;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  user:any = {};

  step: string = 'metodo';
  metodoPago:string = 'efectivo';

  loading:boolean = false;

  paymentInfo: any;

  showInfo: boolean = false;

  pulseraInfo:any = {};

  tod:any = new Date();

  disableButtons: boolean = false;

  types: any = [];

  type:any = {};

  metodo: string = '';

  address:any = {};

  idPago:any = '';

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef, private route: ActivatedRoute,
    private _userService: UserService, private _checkoutService: PaymentContinueService,
    private _checkout2: CheckOutService,
    private viewScroller: ViewportScroller, private _router: Router
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
   }

  ngOnInit() {
    this._checkout2.setStatus('error');
    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        this.user = user;

        // Mark for check
        this.cd.markForCheck();
    });

    // Subscribe to manilla changes
    this._checkoutService.manillaInfo$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((pulsera: any) => {
        this.pulseraInfo = pulsera;
        if(pulsera?.otros?.name){
          // Navigate to the redirect url
          this._router.navigateByUrl('user/pulseras');
          this.Toast.fire({
            icon: 'error',
            title: 'La pulsera ya cuenta con un metodo de pago.'
          });
        }else{
          if(pulsera.pagoId){
            this.getOrder(pulsera?.pagoId?._id);
          }else{
            this.showInfo = true;
          }
        }
        
        // Mark for check
        this.cd.markForCheck();
    });

    // Subscribe to manilla changes
    this._checkoutService.tipos$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((types: any) => {
        this.types = types;

        // Mark for check
        this.cd.markForCheck();
    });

    // Subscribe to manilla changes
    this._checkout2.address$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((address: any) => {
        this.address = address;

        // Mark for check
        this.cd.markForCheck();
    });

    // Subscribe to user changes
    // this._checkoutService.paymentInfo$
    // .pipe(takeUntil(this._unsubscribeAll))
    // .subscribe((data: any) => {
    //     this.paymentInfo = data;
    //     this.metodoPago = data.metodo;
    //     this.showInfo = true;
    //     this.getManillaById(data._id);
    // });

    // Subscribe to user changes
    this._checkout2.status$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((status: any) => {
      if(status === 'success'){
        this.step = 'success';
        this.viewScroller.scrollToPosition([0, 0]);
      }

        // Mark for check
        this.cd.markForCheck();
    });


    this._checkout2.idTransaccion$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((id: any) => {
      this.idPago = id;
      // Mark for check
      this.cd.markForCheck();
    });
    
    this.getTypeManilla();
  }

  metodoPagoChange(event:any){
    this.metodoPago = event;
  }

  goToStep(step:any){
    if(step === 'checkout' && this.metodoPago === 'efectivo'){
      this.createPaymentIntent('Efectivo');
    }else{
      if(step === 'checkout' && this.metodoPago === 'tarjeta'){
        this.createPaymentIntent('Stripe');
      }else{
        this.step = step;
        this.viewScroller.scrollToPosition([0, 0]);
      }
    }
  }

  createPaymentIntent(metodo: any):any{
    this.disableButtons = true;
    let data = {
      userId: this.user._id,
      monto: this.type.precio,
      metodo: metodo,
      manillasId: [this.pulseraInfo._id],
    }

    this._checkoutService.generateOrder(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.paymentInfo = response;
        
        if(metodo === 'Efectivo'){
          this.step = 'checkout';
        }

        if(metodo === 'Stripe'){
          this.step = 'checkout';
        }

        this.metodo = response.metodo;

        this.disableButtons = false;

        this.viewScroller.scrollToPosition([0, 0]);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  getTypeManilla(){
    this.types.forEach(type => {
      if(type.name === this.pulseraInfo.tipo){
        this.type = type;
      }
    });
  }

  getOrder(id:any){
    this._checkoutService.getOrder(id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.paymentInfo = response;
        this.metodo = response.metodo;
        this.showInfo = true;
        this.step = 'checkout';
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

}
