import { Injectable } from "@angular/core";
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from "@angular/router";
import { PaymentContinueService } from "./payment-continue.service";
import { Observable } from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class paymentContinueResolver implements Resolve<any>
{
    /**
     * Constructor
     */
    constructor(private _paymentService: PaymentContinueService)
    {
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Resolver
     *
     * @param route
     * @param state
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]>
    {
        return this._paymentService.getManillaById(route.paramMap.get('id'));
    }
}

@Injectable({
    providedIn: 'root'
})
export class tiposPulseraResolver implements Resolve<any>
{
    /**
     * Constructor
     */
    constructor(private _paymentService: PaymentContinueService)
    {
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Resolver
     *
     * @param route
     * @param state
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]>
    {
        return this._paymentService.getTypesManilla();
    }
}