import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardUserComponent } from './dashboard/dashboard-user.component';
import { ManillasUserComponent } from './manillas/manillas-user.component';
import { UserManillasResolver, UserTypesResolver } from '../core/user/user.resolver';
import { PaymentContinueComponent } from './payment-continue/payment-continue.component';
import { paymentContinueResolver, tiposPulseraResolver } from './payment-continue/payment-continue.resolver';

const routes: Routes = [
  { 
    path: 'dashboard', 
    component: DashboardUserComponent 
  },
  {
    path: 'pulseras', 
    component: ManillasUserComponent,
    resolve: {
      manillas: UserManillasResolver,
      types: UserTypesResolver
    }
  },
  {
    path: 'checkout/:id',
    component: PaymentContinueComponent,
    resolve: {
      pulsera: paymentContinueResolver,
      types: tiposPulseraResolver
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoutingModule { }
