import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {WindowRef} from "../windowRef";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute} from "@angular/router";
import Swal from 'sweetalert2';
import { environment } from 'src/environments/environment';
import { UserService } from 'src/app/core/user/user.service';
import { Subject, takeUntil } from 'rxjs';
import { CheckOutService } from '../checkout-page/checkout-page.service';

@Component({
  selector: 'app-checkout-efective',
  templateUrl: './checkout-efective.component.html',
  styleUrls: ['./checkout-efective.component.scss']
})
export class CheckoutEfectiveComponent implements OnInit {

  @Input() adInfo: any;
  @Input() paymentInfo: any;
  private readonly STRIPE!: any; //TODO: window.Stripe
  private elementStripe!: any;
  form: FormGroup = new FormGroup({})
  id!: string;
  orderData!: any;
  Toast:any;

  address: any;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  user:any = {};

  showDatosEnvio: boolean = true;

  loading:boolean = false;

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef, private route: ActivatedRoute,
    private _userService: UserService, private _checkoutService: CheckOutService
    ) {
    this.STRIPE = window.Stripe(environment.stripe_pk);
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
  }

  ngOnInit(): void {
    this._checkoutService.setStatus('error');
    this.form = this.fb.group({
      amount: ['', [Validators.required, Validators.min(1), Validators.max(100000)]],
    })

    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        this.user = user;

        // Mark for check
        this.cd.markForCheck();
    });

    this.loadDetail();
    this.createStripeElement();
  }

  loadDetail(): void {
    this._checkoutService.getOrder(this.paymentInfo._id).subscribe(
      (data) => {
        this.orderData = data;
        if (data.estado.includes('succe')) {
          this.form.disable()
          this.Toast.fire({
              icon: 'error',
              title: 'Error en la orden, el pago ya ha sido generado.'
            });
          this.loading = true;
        }
        this.form.patchValue({
          amount: data.monto
        })
        this.cd.markForCheck();
    })
  }

  private createStripeElement = () => {
    const style = {
      base: {
        color: '#000000',
        fontWeight: 500,
        fontSize: '20px',
        '::placeholder': {
          color: '#E3E2EC',
        },
      },
      invalid: {
        color: '#dc3545',
      },
    };

    //TODO: SDK de Stripe inicia la generacion de elementos
    this.elementStripe = this.STRIPE.elements({
      fonts: [
        {
          cssSrc:
            'https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap',
        },
      ],
    });


    const address = this.elementStripe.create('address', {
      mode: 'shipping', allowedCountries:['MX'],
      defaultValues: {
        name: `${this.user.name}`,
        address: {
          line1: `${this.user.direccion}`,
          line2: '',
          city: '',
          state: '',
          postal_code: '',
          country: 'MX',
        },
        phone: `${this.user.telefono}`
      },
      fields: {
        phone: 'always',
      },
      validation: {
        phone: {
          required: 'never',
        },
      },
    });

    //TODO: SDK Montamos los elementos en nuestros DIV identificados on el #id
    address.mount('#email');
    this.address = address;
  }

  async initPay(){
    let address = this.elementStripe.getElement('address');
    const {complete, value} = await address.getValue();

    if(complete){
      this.loading = true;
      this.setAddress(value);
      this.editarPaymentIntent(this.paymentInfo._id, {otros: {envio: value, precio: this.orderData.monto}});
    }
  }

  setAddress(data:any):void{
    this._checkoutService.setAddessPulsera({otros: data}, this.paymentInfo.manillasId[0]).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: 'success',
          title: 'La direccion de entrega de la pulsera ha sido agregada con exito.'
        });
        this._checkoutService.setStatus('success');
        this._checkoutService.setAddress(data);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  async prueba(){
    this.form.disable();
    this.loading = true;
    let address = this.elementStripe.getElement('address');

    address.update({
      disabled: true
    })

    // const {complete, value} = await address.getValue();

    // if(complete){
    //   console.log(value);
    // }
  }

  disableInputs(){
    this.loading = true;
  }

  enableInputs(){
    this.loading = false;
  }

  editarPaymentIntent(id:any, data:any){
    this._userService.editarPaymentIntent(id, data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        
      },(error) => {
    
      }
    );
  }
}
