import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, firstValueFrom, tap } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PaymentContinueService {

  private url: string = `${environment.urlWeb}`;
  private _status: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _paymentInfo: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _manillaInfo: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _tipos: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  /**
   * Constructor
   */
  constructor(private _httpClient: HttpClient) {}

  /**
   * Getter for user types
   */
  get status$(): Observable<any[]> {
    return this._status.asObservable();
  }

  /**
   * Getter for user payment info
   */
  get paymentInfo$(): Observable<any[]> {
    return this._paymentInfo.asObservable();
  }

  /**
   * Getter for user manilla info
   */
  get manillaInfo$(): Observable<any[]> {
    return this._manillaInfo.asObservable();
  }

  /**
   * Getter for user tipos info
   */
  get tipos$(): Observable<any[]> {
    return this._tipos.asObservable();
  }

  setStatus(status:any): any{
    this._status.next(status);
  }

  generateOrder(data: any): Observable<any> {
    return this._httpClient.post(`${this.url}pagos/crearIntento`, data);
  }

  sendPayment(token: string, id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/generarMetodo/${id}`, {
        token: token,
      })
    );
  }

  getOrder(id: any): Observable<any> {
    return this._httpClient.get(`${this.url}pagos/obtenerIntento/${id}`).pipe(
      tap((response) => {
          this._paymentInfo.next(response);
      }));
  }

  confirmPayment(id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/confirmar/${id}`, {})
    );
  }

  getTypesManilla(): Observable<any> {
    return this._httpClient.get(`${this.url}tipos`).pipe(
      tap((response) => {
          this._tipos.next(response);
      }));
  }

  getManillaById(id:any): Observable<any>{
    return this._httpClient.get<any[]>(`${this.url}manillas/findById/${id}`).pipe(
      tap((response) => {
          this._manillaInfo.next(response);
      }));
}
}
