import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {WindowRef} from "../windowRef";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute} from "@angular/router";
import Swal from 'sweetalert2';
import { environment } from 'src/environments/environment';
import { UserService } from 'src/app/core/user/user.service';
import { Subject, takeUntil } from 'rxjs';
import { CheckOutService } from './checkout-page.service';

declare global {
  interface Window {
    Stripe?: any;
  }
}

@Component({
  selector: 'app-checkout-page',
  templateUrl: './checkout-page.component.html',
  styleUrls: ['./checkout-page.component.scss']
})
export class CheckoutPageComponent {
  @Input() paymentInfo: any;
  private readonly STRIPE!: any; //TODO: window.Stripe
  private elementStripe!: any;
  cardNumber: any;
  cardCvv: any;
  cardExp: any;
  form: FormGroup = new FormGroup({})
  id!: string;
  orderData!: any;
  Toast:any;

  address: any;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  user:any = {};

  showDatosEnvio: boolean = true;

  loading:boolean = false;

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef, private route: ActivatedRoute,
    private _userService: UserService, private _checkoutService: CheckOutService
    ) {
    this.STRIPE = window.Stripe(environment.stripe_pk);
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
  }

  ngOnInit(): void {
    this._checkoutService.setStatus('error');
    this.form = this.fb.group({
      amount: ['', [Validators.required, Validators.min(1), Validators.max(100000)]],
      cardNumber: [false, [Validators.required, Validators.requiredTrue]], //TODO true | false
      cardCvv: [false, [Validators.required, Validators.requiredTrue]],//TODO true | false
      cardExp: [false, [Validators.required, Validators.requiredTrue]],//TODO true | false
    })

    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        this.user = user;

        // Mark for check
        this.cd.markForCheck();
    });

    this.loadDetail();
    this.createStripeElement();
  }

  loadDetail(): void {
    this._checkoutService.getOrder(this.paymentInfo._id).subscribe(
      (data) => {
        this.orderData = data;
        if (data.estado.includes('succe')) {
          this.form.disable()
          this.Toast.fire({
              icon: 'error',
              title: 'Error en la orden, el pago ya ha sido generado.'
            });
          this.loading = true;
        }
        this.form.patchValue({
          amount: data.monto
        })
        this.cd.markForCheck();
    })
  }

  private createStripeElement = () => {
    const style = {
      base: {
        color: '#000000',
        fontWeight: 500,
        fontSize: '20px',
        '::placeholder': {
          color: '#E3E2EC',
        },
      },
      invalid: {
        color: '#dc3545',
      },
    };

    //TODO: SDK de Stripe inicia la generacion de elementos
    this.elementStripe = this.STRIPE.elements({
      fonts: [
        {
          cssSrc:
            'https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap',
        },
      ],
    });

    //TODO: SDK Construimos los inputs de tarjeta, cvc, fecha con estilos
    const cardNumber = this.elementStripe.create('cardNumber', {
      placeholder: '4242 4242 4242 4242',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });
    const cardExp = this.elementStripe.create('cardExpiry', {
      placeholder: 'MM/AA',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });
    const cardCvc = this.elementStripe.create('cardCvc', {
      placeholder: '000',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });

    const address = this.elementStripe.create('address', {
      mode: 'shipping', allowedCountries:['MX'],
      defaultValues: {
        name: `${this.user.name}`,
        address: {
          line1: `${this.user.direccion}`,
          line2: '',
          city: '',
          state: '',
          postal_code: '',
          country: 'MX',
        },
        phone: `${this.user.telefono}`
      },
      fields: {
        phone: 'always',
      },
      validation: {
        phone: {
          required: 'never',
        },
      },
    });

    //TODO: SDK Montamos los elementos en nuestros DIV identificados on el #id
    cardNumber.mount('#card');
    cardExp.mount('#exp');
    cardCvc.mount('#cvc');
    address.mount('#email');

    this.cardNumber = cardNumber;
    this.cardExp = cardExp;
    this.cardCvv = cardCvc;
    this.address = address;

    //TODO: Escuchamos los eventos del SDK
    this.cardNumber.addEventListener('change', this.onChangeCard.bind(this));
    this.cardExp.addEventListener('change', this.onChangeExp.bind(this));
    this.cardCvv.addEventListener('change', this.onChangeCvv.bind(this));
  }

  async initPay(): Promise<any> {
    let address = this.elementStripe.getElement('address');
    const {complete, value} = await address.getValue();

    if(complete){
      try {
        this.showDatosEnvio = false;
        this.disableInputs();
        this.form.disable();
        //TODO: SDK de Stripe genera un TOKEN para la intencion de pago!
        const {token} = await this.STRIPE.createToken(this.cardNumber);
  
        //TODO: Enviamos el token a nuesta api donde generamos (stripe) un metodo de pago basado en el token
        //TODO: tok_23213
        const data = await this._checkoutService.sendPayment(token.id, this.paymentInfo._id);
        this._checkoutService.setIdTransaccion(data.payment_method);
        //TODO: Nuestra api devolver un "client_secret" que es un token unico por intencion de pago
        //TODO: SDK de stripe se encarga de verificar si el banco necesita autorizar o no
        this.STRIPE.handleCardPayment(data.client_secret)
          .then(async () => {
  
            //TODO: 👌 Money Money!!!
            this.Toast.fire({
              icon: 'success',
              title: 'Pago realizado con exito'
            });
  
            //TODO: Enviamos el id "localizador" de nuestra orden para decirle al backend que confirme con stripe si es verdad!
            await this._checkoutService.confirmPayment(this.paymentInfo._id);
            this.setAddress(value);
            this.editarPaymentIntent(this.paymentInfo._id, {otros: {envio: value, precio: this.orderData.monto}});
          })
          .catch(() => {
            this.showDatosEnvio = true;
            this.enableInputs();
            this.Toast.fire({
              icon: 'error',
              title: 'Error en el pago'
            });
            this.form.enable();
            this._checkoutService.setStatus('error');
          })
      } catch (e) {
        this.showDatosEnvio = true;
        this.enableInputs();
        this.form.enable();
        this.Toast.fire({
          icon: 'error',
          title: 'Algo ocurrio mientras se procesaba el pago'
        });
        this._checkoutService.setStatus('error');
        // this.toaster.open({text: 'Algo ocurrio mientras procesaba el pago', caption: 'ERROR', type: 'danger'})
      }
    }
  }

  setAddress(data:any):void{
    this._checkoutService.setAddessPulsera({otros: data}, this.paymentInfo.manillasId[0]).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: 'success',
          title: 'La direccion de entrega de la pulsera ha sido agregada con exito.'
        });
        this._checkoutService.setStatus('success');
        this._checkoutService.setAddress(data);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  async prueba(){
    // this.form.disable();
    // this.loading = true;
    // this.cardNumber.update({disabled:true});
    // this.cardExp.update({disabled:true});
    // this.cardCvv.update({disabled:true});
    let address = this.elementStripe.getElement('address');

    // address.update({
    //   disabled: true
    // })

    const {complete, value} = await address.getValue();

    if(complete){
      console.log(value);
    }
  }

  disableInputs(){
    this.loading = true;
    this.cardNumber.update({disabled:true});
    this.cardExp.update({disabled:true});
    this.cardCvv.update({disabled:true});
  }

  enableInputs(){
    this.loading = false;
    this.cardNumber.update({disabled:false});
    this.cardExp.update({disabled:false});
    this.cardCvv.update({disabled:false});
  }

  //TODO: Manejadores de validacion de input de stripe

  onChangeCard({error}: any) {
    this.form.patchValue({cardNumber: !error});
  }

  onChangeCvv({error}: any) {
    this.form.patchValue({cardCvv: !error});
  }

  onChangeExp({error}: any) {
    this.form.patchValue({cardExp: !error});
  }

  editarPaymentIntent(id:any, data:any){
    this._userService.editarPaymentIntent(id, data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        
      },(error) => {
    
      }
    );
  }
}
